spant:before {
  content: "";
  animation: animate infinite 40s;
}

@keyframes animate {
  0% {
    content: "Website Development";
  }
  14% {
    content: " Android App Development";
  }
  28% {
    content: "IOS App Development";
  }
  42% {
    content: "Video Editing";
  }
  56% {
    content: "Graphic Designing";
  }
  70% {
    content: "Social Media Marketing";
  }
  100% {
    content: "Motion Graphics Advertisement";
  }
}

